@import url(https://fonts.googleapis.com/css?family=Baloo|Nunito);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  font-family: Nunito;
  margin: 0;
  min-height: 100vh;
  padding-top: 2rem;
  background: #26c6da;
}

.main {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem 3%;
  background: #fff;
}

.site-title {
  font-family: Baloo;
}

.article-section {
  height: 60vh;
  overflow: scroll;
  background-color: rgba(0,0,0,.1);
}

img {
  max-width: 100%;
  height: auto;
}

@media screen and (max-width: 709px) {
  .wrapper {
    padding-left: 3%;
    padding-right: 3%;
  }
}
